<template>
    <div>
        <TodoItem
            v-for="todo of todos" :key="todo"
            v-bind:todo="todo"
            v-on:remove-todo="removeTodo"
        />
    </div>
</template>

<script>
import TodoItem from './TodoItem.vue'

export default {
  name: 'App',
  props: ['todos'],
  components: {
    TodoItem
  },
  methods: {
      removeTodo(id) {
          this.$emit('remove-todo',id)
      }
  },
}
</script>