<template>
    <div class="container">
        <div class="content"
            v-bind:class="{complite: todo.status}"
            v-on:change="todo.status = !todo.status"
        >
            <input type="checkbox" v-bind:id="'checkboxTodo' + todo.id">
            <label v-bind:for="'checkboxTodo' + todo.id"> {{todo.title}} </label>
        </div>
        <button @click="$emit('remove-todo', todo.id)">Удалить</button>
    </div>
</template>

<script>
export default {
    props: ['todo'],
}
</script>

