<template>
  <div>
    <h2>Список дел</h2>
    <TodoList
      v-bind:todos="todos"
      v-on:remove-todo="removeTodo"
    />
    <AddTodo
      v-on:add-todo="addTodo"
    />
    <router-link to="/">Главная</router-link>
  </div>
</template>

<script>
import TodoList from '../components/TodList.vue'
import AddTodo from '../components/addTodo.vue'

export default {
  name: 'App',
  components: {
    TodoList, AddTodo
  },
  data() {
    return {
      todos: [
        {id: 1, title: 'Помочь соседу', status: false},
        {id: 2, title: 'Выгулять свою собаку', status: false},
        {id: 3, title: 'Вспомнить, что у тебя не было собаки', status: false},
        {id: 4, title: 'Вернуть собаку соседу который её уже месяц ищет', status: false},
      ]
    }
  },
  methods: {
    removeTodo(id) {
      this.todos = this.todos.filter(t => t.id !==id)
    },
    addTodo(todo){
      this.todos.push(todo)
    }
  },
}
</script>

<style scoped>
    a{
        text-decoration: none;
        color: black;
        margin-top: 30px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        padding: 10px;
        background-color: rgb(146, 146, 146);
        border-radius: 5px;
    }

    div{
      max-width:700px;
      margin: 0 auto;
    }
</style>